import React, {FC} from 'react';
import {
    StyledContainer,
    StyledItems,
    StyledParameters, StyledSection,
    StyledTitle
} from "./WeatherParameters.styles";
import {useTypedSelector} from "../../redux/hooks/useTypeSelector";

const WeatherParameters: FC = () => {
    const weathers = useTypedSelector(state => state.weather.weather)
    const weats = useTypedSelector(state => state.weather)
    console.log(weats)
    return (
        <StyledSection>
            <StyledContainer>
                <StyledItems>
                    <StyledParameters>
                        {Math.round(weathers.feels_lik)}
                        <StyledTitle></StyledTitle>
                    </StyledParameters>
                    <StyledParameters>
                        {Math.round(weathers.humidity)}%
                        <StyledTitle></StyledTitle>
                    </StyledParameters>
                    <StyledParameters>
                        {weathers.max_temp}
                        <StyledTitle> t</StyledTitle>
                    </StyledParameters>
                </StyledItems>
                <StyledItems>
                    <StyledParameters>
                        {Math.round(weathers.pressure)}
                        <StyledTitle></StyledTitle>
                    </StyledParameters>
                    <StyledParameters>
                        {Math.round(weathers.wind)}
                        <StyledTitle> /</StyledTitle>
                    </StyledParameters>
                    <StyledParameters>
                        {weathers.min_temp}
                        <StyledTitle> t</StyledTitle>
                    </StyledParameters>
                </StyledItems>
            </StyledContainer>
        </StyledSection>
    );
};

export default WeatherParameters;